import { Component, OnInit } from '@angular/core';
import { TranslateConfigService } from '../../services/translate-config.service';
import { TranslateService } from '@ngx-translate/core';
import { UtilService } from 'src/providers/utils/utils.service';
import { ConfigProvider } from 'src/providers/utils/config';
import { LoggerServiceProvider } from 'src/providers/logger-service/logger-service';

@Component({
  selector: 'app-plan-descriptions',
  templateUrl: './plan-descriptions.page.html',
  styleUrls: ['./plan-descriptions.page.scss'],
})
export class PlanDescriptionsPage {
  selectedLanguage:string;
  policyData : any;
  planDescriptionData : any = null;
  planType:any;
  planName:any;
  policyNo:any;
  errorMsg :any;
  constructor(
    private translateConfigService: TranslateConfigService,
    public translate:TranslateService,private utils : UtilService,
    private logger : LoggerServiceProvider, private config: ConfigProvider

  ) {
    this.selectedLanguage = this.translateConfigService.getDefaultLanguage();
      this.translateConfigService.setLanguage('en');
      this.config.currentPage.pageName = "PlanDescriptionsPage";
      this.policyData=  history.state.policyDetailsData;
      this.planDescriptionData = this.policyData['planDescription'];
      this.planName=  this.policyData.planDescription.plan;
      this.planType=   this.planDescriptionData ['planType'];
      this.policyNo=  this.planDescriptionData ['policyNo'];
      this.utils.analytics("planDescriptions");
      this.errorMsg = this.translate.instant('generic_msgs.no_data');
   }
   ionViewDidLoad() {
    this.logger.log('ionViewDidLoad PlanDescriptionsPage');
  }


}
